import React, {Component} from 'react';
import {View, Text} from 'react-native';
import {observer, Provider, inject} from 'mobx-react/native'
import {Button, ListItem, Icon, Input, Container, Content, Header, List, Left, Right, Body} from 'native-base';

const styles = {
    textStyles: {
        color: 'white'
    }
}


@inject(
    "stores",
    "globals"
) @observer
class Main extends Component {

    componentDidMount(){
        this.props.globals.login();
    }

    render() {
        let {UiStore, NumberStore, ColorStore} = this.props.stores;
        let globals = this.props.globals;

        const viewStyle = {width: 50, height: 50, backgroundColor: ColorStore.currentColor};

        return (
            <Container>
            <Content>
                <Header style={{flex: 1, flexDirection: 'row'}}>
                    <Left>
                        <Button transparent>
                            {NumberStore.currentNumber}
                        </Button>
                    </Left>
                    <Body>
                        <Title>{UiStore.loggedIn ? "Logged In" : "Login"}</Title>
                    </Body>
                    <Right >
                        <Button transparent>
                            {NumberStore.currentNumber}
                        </Button>
                    </Right>
                </Header>
                <View style={{flex: 1, flexDirection: 'row'}}>
                    <Button danger onPress={() => ColorStore.setColor(1)}>Red</Button>
                    <Button onPress={() => ColorStore.setColor(0)}>Blue</Button>
                    <Button success onPress={() => ColorStore.setColor(2)}>Green</Button>
                    <Button onPress={() => NumberStore.add()}>Add</Button>
                    {UiStore.displayUndo && <Button onPress={() => globals.resetState()}>Undo</Button>}
                </View>
                <View style={{flex: 1, flexDirection: 'row', backgroundColor: 'blue'}}>
                    <List>
                        <ListItem style={{backgroundColor: 'blue', width: 80}}>
                            <Icon active name='home' />
                            <Input placeholder='Icon Textbox'/>
                        </ListItem>

                        <ListItem>
                            <Input placeholder='Alignment in Textbox'/>
                        </ListItem>
                        <ListItem>
                            <Text>Simon Mignolet</Text>
                        </ListItem>
                        <ListItem>
                            <Text>Nathaniel Clyne</Text>
                        </ListItem>
                        <ListItem>
                            <Text>Dejan Lovren</Text>
                        </ListItem>
                    </List>
                </View>
            </Content>
            </Container>
        );
    }
};

export default Main